import React from 'react';
import Post from "./Post/Post";
import p from './MyPosts.module.css'

type InPostData = {
    id: number
    post: string
    likeCount: number
    dislike: number
}
type MyPostsData = {
    postData: Array<InPostData>
}
const MyPosts = (props: MyPostsData) => {
    let postElement = props.postData.map(post => <Post postText={post.post} like={post.likeCount}
                                                       dislike={post.dislike}/>)
    return (
        <div className={p.posts}>
            My posts
            <div>
                <textarea></textarea>
                <div>
                    <button>Add post</button>
                </div>
            </div>
            <div>
                {postElement}
            </div>
        </div>
    );
};

export default MyPosts;